.services {
  text-align: center;
  background: $grey-block;
  padding-bottom: 80px;

  @include mediaTablet {
    padding-bottom: 60px;
  }

  @include mediaMobile {
    padding-bottom: 40px;
  }


}

.content-services__wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 48px;

  @include mediaTablet {
    display: flex;
    justify-content: center;
    gap: 24px;
  }

  @include mediaMobile {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 30px;
  }
}

.content-services {
  background: rgba(255, 255, 255, 1);

  &__frame {
    width: 410px;
    height: 250px;
    text-align: center;
    padding: 50px 32px;

    @include mediaTablet {
      max-width: 320px;
      max-height: 225px;
      padding: 30px 32px;
    }

    @include mediaMobile {
      max-width: 220px;
      max-height: 215px;
      padding: 12px 10px;
    }


    &-img {
      margin-bottom: 18px;
    }

    &-title {
      font-size: 20px;
      font-weight: 800;
      margin-bottom: 10px;
    }

    &-text {
      font-size: 14px;
      font-weight: 400;

    }

    &:hover {
      color: white;
      background: rgba(79, 143, 240, 1);

      path {
        fill: #ffffff;
      }
    }


  }

}